import React from 'react';
import PropTypes from 'prop-types';
import bridge from "@vkontakte/vk-bridge";


import { Panel, PanelHeader, Header, Button, Group, Cell, Div, Avatar, Snackbar } from '@vkontakte/vkui';

const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader>Cookies</PanelHeader>
		{fetchedUser &&
		<Group header={<Header mode="secondary">Профиль:</Header>}>
			<Cell
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</Cell>
		</Group>}

		<Group header={<Header mode="secondary">Кнопки навигации</Header>}>
			<Div>
				<Button stretched size="l" mode="secondary" onClick={async () => {
				const test = await bridge.send("VKWebAppShowNativeAds", {ad_format:"reward"});
				if(test.result == true) {
					
				} else {

				}

				}}>
				Посмотреть рекламу
				</Button>
				<br/>
				<Button stretched size="l" mode="secondary" onClick={async () => {
					setInterval(async () => {
						bridge.send("VKWebAppShowNativeAds", {ad_format:"reward"});
					}, 6000)
				}}>
				Авто реклама(идеально работает токо на пк)
				</Button>
			</Div>
		</Group>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
